<template>
  <ul class="demo">
    <li v-for="(todoObj, index) in todo" :key="todoObj.id">
        <input type="checkbox" :checked="todoObj.done" @change="handelCheck(todoObj.id)">
        <span v-show="!todoObj.edit">{{todoObj.title}}</span>
        <!-- <span>{{ index }}</span> -->
        <input ref="focusGet" v-show="todoObj.edit"  v-model="todoObj.title" @blur="handelBlur(todoObj)" type="text" v-focus>
        <button v-show="!todoObj.edit" @click="handelEdit(todoObj, index)" >编辑</button>
        &nbsp;
        <button @click="listDelete(todoObj.id)">删除</button>
    </li>
  </ul>
</template> 

<script>
// import { nextTick } from 'vue'
export default {
    name:'MyList',
    props:['todo','checkTodo','listDelete1'],
    methods:{
        handelCheck(id){
            this.checkTodo(id)
        },
        listDelete(id){
            if(confirm('确定删除吗？')){
                this.listDelete1(id)
            }
        },
        handelEdit(todoObj, index){
            if(todoObj.hasOwnProperty('edit')){
                todoObj.edit = true
            }
            else{
                this.$set(todoObj,'edit',true)
            }
            this.$nextTick(function(){
                this.$refs.focusGet[index].focus()
            });
        },
        handelBlur(todoObj){
            if(!todoObj.title.trim()){
                alert("不能修改为空值");
                todoObj.edit = true
            }
            else{
                todoObj.edit = false
            }
                    // this.$bus.$emit('updateTodoobj', todoObj.title)
        }
    },
    directives:{
        focus:{
            update(element) {
                element.focus()
            },
        }
    }
}
</script>

<style scoped>
    .demo{
        background-color:rgb(19, 184, 196) ;
    }
    .demo li{
        list-style: none;
        
    }
    .demo span{
        float:left;
    }
    .demo button{
        float: right;
        display: none;
    }
    li:hover button{
        background-color: #12a3d8;
        display: block;
        
    }
</style>