<template>
    <div class="demo">
        <h1>当前求和为：{{sum}}</h1>
        <h3>当前sum扩大10倍后的结果是{{bigSum}}</h3>
        <h3>获取到的数据有: {{ msg }}</h3>
        <select v-model.number="n">
            <option value="1">1</option>
            <option value="2">2</option>
            <option value="3">3</option>
        </select>
        <button @click="increment(n)">+</button>
        <button @click="decrement(n)">-</button>
        <button @click="incrementOdd(n)">当前求和为奇数再加</button>
        <button @click="addMessage()">点击获取数据</button>
        <!-- <h3>我在{{$store.state.school}}学习{{ $store.subject}}</h3> -->
    </div>
</template>

<script>
import {mapGetters, mapState, mapMutations, mapActions} from 'vuex'
export default {
    name:'CountTest',
    data(){
        return {n:1,}
    },
    methods:{
        // increment(){
        //     this.$store.commit('JIA', this.n)
        // },
        // decrement(){
        //     this.$store.commit('JIAN', this.n)
        // },

        // ...mapMutations({increment:'JIA',decrement:'JIAN'}),
        //模块化开启命名空间后
        // this.$store.commit('countAbout/JIA', this.n)
        ...mapMutations('countAbout', {increment:'JIA', decrement:'JIAN'}),
        //同理可以用数组的方法，但是事件和方法名需要一致
        // incrementOdd(){
        //     this.$store.dispatch('jiaOdd', this.n)

        // ...mapActions({incrementOdd:'jiaOdd'}),
        //模块化开启命名空间后
        ...mapActions('countAbout', {incrementOdd:'jiaOdd', addMessage:'addMessage'})

        
        

    },
    watch:{
        sum:{
            handler(newVal){
                sessionStorage.setItem('sum',JSON.stringify(newVal))
            }
        }
    },
    computed:{
        // bigSum(){
        //     return this.$store.getters.bigSum
        // },
        //借助mapGeters生成计算属性，从getters读取数据

        // ...mapGetters(['bigSum']),
        //模块化开启命名空间后
        ...mapGetters('countAbout',['bigSum']),

        //借助mapState生成计算属性
        // ...mapState(['sum']),
        //模块化开启命名空间后
        ...mapState('countAbout', ['sum','msg'])
        // ...mapState({he:'sum'})

    }
}
</script>

<style scoped>
    button{margin-left:5px;}
/* scoped样式在组件生效防止冲突 */
/* 组件样式 */    
    .demo{
        background-color: rgba(131, 198, 137, 0.644);
    };
</style>