import axios from "axios"
export default{
    namespaced:true,//开启名命空间
    state:{
        sum: sessionStorage.getItem('xxxx') || 0,
        school: localStorage.getItem('xxxx') || '实验中学',
        index: [JSON.parse(sessionStorage.getItem('msg'))].index ||1,
        msg: JSON.parse(sessionStorage.getItem('msg')) || []
    },
    mutations:{
        JIA(state, value){
            state.sum+=value
            // console.log(state);  
        },
        JIAN(state, value){ 
            state.sum-=value
        }
    },
    actions:{
        jiaOdd(context, value){
            if(context.state.sum % 2){
                context.commit('JIA',value)
                // console.log(context);
            }
        },
        //actions中拿不到state
        addMessage(context){
            axios.get('https://autumnfish.cn/api/joke?a=100').then(
                response => {
                    // console.log(context.state.msg);
                    //console.log(response.data);
                    context.state.msg.unshift(response.data)
                    sessionStorage.setItem('msg', JSON.stringify(context.state.msg))
                },
                error => {  
                    alert(error.message)
                } 
                // function(msg){
                    //     console.log(msg.data);
                    //}
                    )
            }
        },

    getters:{
        bigSum(state){
            return state.sum  * 10
        },
    },
}   
