<template>
    <div>
        <ul> 
        <!-- <li> <a href="/message1">message001</a>&nbsp;&nbsp; </li> 
        <li> <a href="/message2">message002</a>&nbsp;&nbsp; </li> 
        <li> <a href="/message/3">message003</a>&nbsp;&nbsp; </li>  -->

        <!-- 特别注意：路由携带params参数时，若使用to的对象写法，则不能使用path配置项，必须使用name配置！ -->

            <li v-for="m in msgList" :key="m.id">
                <router-link :to="{
                    name:'msgtext',
                    params:{
                        id:m.id,
                        title:m.title
                    }
                }">{{m.title }}
                </router-link>
            </li>
        </ul>
        <hr/>
        <router-view></router-view>
    </div>
</template>

<script>
export default {
    name:'MessageName',
    data(){
        return{
            msgList:[
            {id:'001',title:'消息001'}, 
            {id:'002',title:'消息002'}, 
            {id:'003',title:'消息003'}
            ]
        }
    }
}

</script>

