<template>
    <h2 class="demo2">
        <input type="text" placeholder="请输入你想输入的内容，回车确认" v-model="title" @keyup.enter="add">
    </h2>
</template>

<script>
import { nanoid } from 'nanoid';
    export default {
        name:'MyHeader',
        props:['addTod'],
        data(){
            return{
                title:''
            }
        },
        methods:{
            add(){
                // !x.trim()的含义是先对字符串x使用trim()方法去除两端空格，
                // 然后对结果进行逻辑非操作（取反）。
                if(!this.title.trim()) return alert("不能输入空值")
                const todoObj = {
                    id:nanoid(),
                    title:this.title,
                    done:false
                }
                this.addTod(todoObj)
                this.title = ''
            },
        }
    }
</script>

<style scoped>
    .demo2 input{
        width: 560px;
        height: 28px;
        font-size: 14px;
        background-color: #77b2ba;
    }
</style>