<!-- 用插件 < + v 快速添加conponent,<script></script>, <style></style>等标签 -->
<template>
     <div class="demo">
        <h2 class="qwe">学校名字：{{schoolName}}</h2> 
        <h2>学校地址：{{address}}</h2>    
        <button @click="showName">点击弹出学校名字</button>
        <button @click="showNumber">打印n+1值</button>
        <button @click="showTest0"> 全局事件总线 点击弹出学校地址</button>
    </div>   
</template>

<script>
        //组件交互相关代码
    // const School = Vue.extend({
    //         //data写成函数的形式，避免组件复用是数据存在引用关系
    //         data(){
    //             return {
    //                 schoolName:'实验中学',
    //                 address:'重庆'
    //             }
    //         },
    //         methods: {
    //             showName(){
    //                 alert(this.schoolName)
    //             },
    //         },
    //         });
    //这里是默认暴露 另外还有分别暴露、统一暴露
    // export default School
    import mixin from '../mixin.js'
    export default{
        name:'SchoolName',
        //自己最终定义的组件名字
        data(){
            return {
                schoolName:'实验中学',
                address:'重庆'
            }
        },
        methods: {
            showName(){
                alert(this.schoolName)
            },
            showTest0(){
                //全局事件总线
                this.$bus.$emit('showTest', this.address)
                // 自定义事件
                // this.$emit('showTest', this.address)
            }
            
        },
        mixins:[mixin]
    }
</script>
<style lang="less">
    .demo{
        background-color: pink;
        .qwe{
            font-size: 40px;
        }
    }
</style>