<template>
    <div>
        <div class="text"></div>
        <BannerName></BannerName>
        <router-link replace class="list-group-item" to="/home"><button>Home</button></router-link>
        <router-link replace class="list-group-item" to="/about"><button>About</button></router-link>
        <div class="demo"><router-view></router-view>
        </div>  
        <hr/>
        <!-- <img src="./assets/logo.png" alt="logo">     -->
        <CountTest></CountTest>
        <MyHeader :addTod="addTodo"></MyHeader>
        <MyList :todo="todos" :checkTodo="checkTodo" :listDelete1="listDelete"></MyList>
        <!-- 全局事件总线 -->
        <SchoolName></SchoolName>
        <!-- <SchoolName @showTest="showTest1"></SchoolName>  -->
        <!-- 这是自定义事件中,绑定自定义事件 -->
        <!-- 左边是事件，右边是函数 -->
        <StudentName name="李四" sex="女" :age="66">
            <template slot-scope="{msgText}">
                <ul>
                    <h2 v-for="(m, index) in msgText" :key="index">{{ m }}</h2>
                </ul>
            </template>
        </StudentName>
        <!-- age前面用冒号  v-bind 的简写,将冒号里面的"结果"返回给age -->
    </div>
</template>

<script>
    import BannerName from './components/Banner.vue'
    import MyHeader from './components/MyHeader.vue';
    import MyList from './components/MyList.vue';
    import SchoolName from './components/School.vue';
    import StudentName from './components/Student.vue';
    import CountTest from './components/CountTest.vue';
    export default {    
        name:'App',
        components:{
            CountTest,
            SchoolName,
            StudentName,
            MyHeader,
            MyList,
            BannerName
            
        },
        data(){
            return{
                todos:JSON.parse(localStorage.getItem('todos')) || []
            }
        },
        methods:{
            addTodo(todoObj){
                this.todos.unshift(todoObj)
            },
            checkTodo(id){
                this.todos.forEach((todo)=>{
                    if(todo.id===id){todo.done = !todo.done}
                })
            },
            showTest1(address){
                alert(address);
            },
            listDelete(id){
                this.todos = this.todos.filter((todo)=>{
                    return todo.id !== id
                })
            }
        },
        watch:{
            todos:{
                deep:true,
                handler(value){
                    localStorage.setItem('todos', JSON.stringify(value))
                    // console.log(value);
                }
            }
        },
        // create(){
        //     if(!todos){
        //         this.todos = []; 
        //     }
        // }
        mounted(){
            this.$bus.$on('showTest',this.showTest1)
            //左边是事件，右边是函数
            //自定义事件
            this.$on('showTest',this.showTest1)
            this.$axios.get('http://localhost:8000/student').then(
                response =>{
                    console.log(response.data);
                },error=>{
                    console.log('hahha ',error.message);
                }
            )
            // let A = () =>{
            //     let a = 1
            //     window.B = ()=>{
            //         console.log(a);
            //     }
            // }
            // A()
            // B()
        },
        beforeDestroy(){
            this.$bus.$off('showTest')
        }
    }
</script>

