import Vue from 'vue'
import App from './App.vue'
import store from './store'
//引入该插件
import VueRouter from 'vue-router'
//引入该路由
import router from './router'
import axios from 'axios'
Vue.use(VueRouter)
Vue.prototype.$axios = axios
// const a = 10
// 测试vue.config.js配置lintOnSave = false 是否成功

new Vue({
    el:'#app',
    //如果index.html中不加<App></App>可以这样
    // template:`<App></App>`,
    render: h => h(App),
    // render (createElement) =>{
    //    createElement(APP)
    // }
    store,
    router:router,
    beforeCreate(){
        Vue.prototype.$bus = this
    },
    //安装全局事件总线
})
