import VueRouter from "vue-router";
import AboutName from '../pages/About.vue'
import HomeName from '../pages/Home.vue'
import NewName from '../pages/New.vue'
import MessageName from '../pages/Message.vue'
import DetailsName from '../components/Details.vue'
import NewsName from '../components/News.vue'

const router = new VueRouter({
    mode:'hash',
    routes:[
        {
            path:'/about',
            component:AboutName,
            meta:{title:'关于'}
        },
        {
            path:'/home',
            component:HomeName,
            meta:{title:'主页'},
            children:[
                {
                    path:'news',
                    component:NewName,
                    meta:{isAuth:true, title:'新闻'},
                    children:[
                        {
                            name:'newstext',
                            path:'new', 
                            component:NewsName,
                            meta:{title:'信息'},
                            props($route){
                                // console.log($route.query.news);
                                return {id:$route.query.id,newItem:$route.query.new}
                            },
                        }
                    ]
                },
                {
                    path:'message',
                    component:MessageName,
                    meta:{isAuth:true,title:'消息'},
                    children:[
                        {
                            name:'msgtext',
                            path:'detail/:id/:title', //'detail',query传参
                            component:DetailsName,
                            meta:{title:'详情'},
                            //路由props的配置
                            // props:true, //把路由组件收到的所有params参数，以props的形式传给Details.vue，
                            //第三种适用params和query
                            props($route){
                                // console.log($route.params.id);
                                return {id:$route.params.id,title:$route.params.title}
                            },
                            beforeEnter(to,from,next){
                                // console.log('独享路由守卫',to,from)
                                if(localStorage.getItem('school') === '666'){
                                    next()
                                }else{
                                    alert('暂无权限查看,你的学校居然不是666')
                                }
                            }
                        }   
                    ]

                }
            ]
        }
    ]
})
router.beforeEach((to,from,next) => { 
    // console.log('beforeEach',to,from) 
    if(to.meta.isAuth){ // 判断当前路由是否需要进行权限控制 
        if(localStorage.getItem('id') === 'root'){ // 权限控制的具体规则 
            next() // 放行 
        }else{ alert('暂无权限查看') } 
    }else{ next() // 放行 
} })

// 全局后置守卫：初始化时、每次路由切换后执行 
router.afterEach((to,from) => { 
    // console.log('afterEach',to,from) 
    if(to.meta.title){ 
        document.title = to.meta.title //修改网页的title 
    }
    else{ document.title = 'vue_test' } 
})

export default router