<template>
   <div>
        <ul class="demo"> 
            <li v-for="n in NewList" :key="n.id">{{n.new}}<button @click="NewsShow(n)">点击查看</button></li> 
        </ul>
        <router-view></router-view>
   </div>
</template>

<script>
export default {
    name:'NewName',
    data(){
        return{
            NewList:[
            {id:'001',new:'新闻001'}, 
            {id:'002',new:'新闻002'}, 
            {id:'003',new:'新闻003'}
            ]
        }
    },
    methods:{
        NewsShow(n){
            // this.$router.back()      this.$router.forward()      this.$router.go()
            // this.$router.replace() 用新的替换掉前一个页面，而push是追加不会破环记录
            //  <router-link replace></router-link> 控制路由跳转时操作浏览器历史记录的模式   替换当前记录，默认是push
            //编程式路由导航 不借助router-link 实现路由跳转
            this.$router.push({
                name:'newstext',
                query:{
                    id:n.id,
                    new:n.new
                }
            })
            // console.log(this.$router);
        }
    },
    beforeRouteEnter(to, from, next){
        console.log(999,'New.vue组件间路由守卫');
        next()
    },
    beforeDestroy(){
        console.log('888NEW.VUE即将被销毁');
    }
}
</script>
<style>
    .demo li{
        list-style: none;
        text-decoration: none;
    }
</style>