<template>
     <div class="demo">
        <h1>{{msg}}</h1>
        <h2>学生名字：{{name}}</h2> 
        <h2>学生年龄：{{myAge}}</h2> 
        <slot :msgText="msgText"></slot>
        <!-- <h2>{{this.n}}</h2> -->
        <button @click="upData">点击年龄增加一岁</button>
        <button @click="showNumber">打印n+1值</button>
        <button @click="changePropsAge">点击修改mixin传过来的n为18</button>
    </div>   
</template>

<script>    
    // export default Vue.extend({
    //     name:'Student',
    //     //自己最终定义的组件名字
    //     data(){
    //         return {
    //             name:'张三',
    //             age:'16'
    //         }
    //     }
    // })
    import mixin from '../mixin.js';
    export default {
        name:'StudentName',
        //自己最终定义的组件名字
        data(){
            return {
                // name:'张三',
                // age:'16'
                msg:'nihao',
                myAge:this.age,
                msgText:['haha, hahahw2, 作用域插槽']
                //不允许对props中的数据直接修改，所以需要使用myAge来代替
            }
        },
        // props:['name', 'age', 'sex'] //简单声明接收
        // props:{
        //     name:String,
        //     age:Number,
        //     sex:String
        // }
        // 接收数据对类型进行限制
        methods:{
            upData(){
                this.myAge++            
            },
            changePropsAge(){
                this.n = 18
            }
        },
        props:{
            name:{
                type:String,
                required:true,
                //name属性是必要的
            },
            age:{
                type:Number,
                default:6
                //若没有给age传入数据则默认是66
            },
            sex:{
                type:String,
                required:true,
            }
        },
        mixins:[mixin],
        beforeUpdate(){
            console.log("666");
        },
        updated() {
            console.log("777");
        },
        //接收数据的同时对数据类型进行限制+默认值的指定+必要性的限制
    }
</script>
<style scoped>
/* scoped样式在组件生效防止冲突 */
/* 组件样式 */    
    .demo{
        background-color: rgba(11, 194, 133, 0.644);
    };
</style>